import React, { SFC } from 'react'
import styled from '@emotion/styled'

export type Kind = 'info' | 'positive' | 'negative' | 'warning'
export type KindMap = Record<Kind, string>

const kinds: KindMap = {
  info: '#5352ED',
  positive: '#2ED573',
  negative: '#FF4757',
  warning: '#FFA502',
}

export interface AlertProps {
  /**
   * Set this to change alert kind
   * @default info
   */
  kind: 'info' | 'positive' | 'negative' | 'warning'
}

const AlertStyled = styled('div')`
  padding: 15px 20px;
  background: white;
  border-radius: 3px;
  color: white;
  background: ${({ kind = 'info' }: AlertProps) => kinds[kind]};
`

export const Alert: SFC<AlertProps> = ({ kind, ...props }) => (
  <AlertStyled {...props} kind={kind} />
)
try {
    // @ts-ignore
    Alert.displayName = "Alert";
    // @ts-ignore
    Alert.__docgenInfo = { "description": "", "displayName": "Alert", "props": { "kind": { "defaultValue": { value: "info" }, "description": "Set this to change alert kind", "name": "kind", "required": true, "type": { "name": "\"info\" | \"positive\" | \"negative\" | \"warning\"" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/Alert.tsx#Alert"] = { docgenInfo: Alert.__docgenInfo, name: "Alert", path: "src/components/Alert.tsx#Alert" };
}
catch (__react_docgen_typescript_loader_error) { }